                const myResultWork = calculateDamageFromDetailSub(statsObj, damageResult, statsObj[dmgUpStat], myArr, isCalc, isCalc, isCalc, my, my, null);
                // HITHIT
                if (myHIT > 1) {
                    myResultWork[2] *= myHIT;
                    if (myResultWork[3] != null) {
                        myResultWork[3] *= myHIT;
                    }
                    myResultWork[4] *= myHIT;
                }
                myResult[2] += myResultWork[2];
                if (myResult[3] != null && myResultWork[3]) {
                    myResult[3] += myResultWork[3];
                }
