function updateStats(
    statsObj: TStats,
    statName: string,
    formulaArr: number | string,
    opt_max: number | string | null = null,
    opt_min: number | string | null = null,
) {
    const value = evalFormula(formulaArr, statsObj, DAMAGE_RESULT_TEMPLATE, opt_max, opt_min);    // DAMAGE_RESULT_TEMPLATE
    if (!isNumeric(value)) {
        console.error(statsObj, statName, formulaArr, value);
    }
    const nameArr = [];
    if (statName == 'HP') {
        nameArr.push('HP');
    } else if (statName.startsWith('')) {
        nameArr.push(statName.replace(/^/, String(statsObj[''])));
    } else if (statName.startsWith('')) {
        ALL_ELEMENTS.forEach(entry => {
            nameArr.push(statName.replace(/^/, entry));
        });
    } else if (statName.startsWith('')) {
        nameArr.push(statName.replace(/^/, '' + String(statsObj[''])));
    } else if (statName.startsWith('')) {
        ALL_ELEMENTS.forEach(entry => {
            nameArr.push(statName.replace(/^/, '' + entry));
        });
    } else {
        nameArr.push(statName);
    }
    const diffStats = {} as { [key: string]: number };
    nameArr.forEach(name => {
        diffStats[name] = value;
    });
    for (const name of Object.keys(diffStats)) {
        if (name in statsObj) {
            if (name.startsWith('') && diffStats[name] > 0) {
                statsObj[name] *= diffStats[name] / 100;    // for 
            } else {
                statsObj[name] += diffStats[name];
            }
        } else {
            statsObj[name] = diffStats[name];
        }
    }
    console.debug(updateStats.name, null, statName, formulaArr, '=>', diffStats);
    return diffStats;
}

/**  */
function calculateDamageTaken(statsObj: TStats, damage: number, element: string) {
    const def = statsObj[''];
    const enemyLevel = statsObj[''];
    const dmgReduction = statsObj[''];
    const res = calculateRes(statsObj[element == '' ? '' : element + '']);
    let result = damage * (1 - def / (def + 5 * enemyLevel + 501)) * (100 - dmgReduction) / 100 * res;
    result = Math.max(0, result);
    return result;
}

